:root {
    --background-black-fret: #246B61;
    --color-black-fret: #6BA099;
    --background-white-fret: #AA6839;
    --color-white-fret: #FFCDAA;
  }
  
  ol {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
  }
  
  li>ol {
    height: 15vh;
    flex-direction: row;  
  }
  
  li {
    list-style-type: none;
  }
  
  li li {
    width: 100%;
    text-align: center;
    padding-top: 0.2rem;
  }
  
  li:nth-child(odd) li:nth-child(odd) {
    background: var(--background-white-fret);
    color: var(--color-white-fret);
    
  }
  li:nth-child(odd) li:nth-child(even) {
    background: var(--background-black-fret);
    color: var(--color-black-fret);
    
  }
  li:nth-child(even) li:nth-child(even) {
    background: var(--background-white-fret);
    color: var(--color-white-fret);
    
  }
  li:nth-child(even) li:nth-child(odd) {
    background: var(--background-black-fret);
    color: var(--color-black-fret);
    
  }
  
  li:nth-child(even) {
    background: #2D4671;
    color: #7689A9;
  }
  
  li:nth-child(odd) {
    background: #408E2F;
    color: #9AD58E;
  }

  li li._1P {
    background: navy !important; 
    color: rgb(207, 202, 202)!important;
  }

  li li._2m {
    background: yellow !important; 
    color: black!important;
  }

  li li._2M {
    background: yellow !important; 
    color: black!important;
  } 
  li li._3m {
    background: orange !important; 
    color: black!important;
  }

  li li._3M {
    background: red !important; 
    color: black!important;
  }

  li li._4P {
    background: olive !important; 
    color: black!important;
  }

  li li._5P {
    background: blue !important; 
    color: black!important;
  }

  li li._6M {
